"use client";

import { useSession, signIn, signOut } from "next-auth/react";

export default function Home() {
  const { data: session } = useSession();

  return (
    <div className="flex flex-col items-center justify-center min-h-screen">
      <h1 className="text-3xl font-bold">Welcome to Your To-Do App</h1>

      {session ? (
        // If user is signed in, show their email and a Logout button
        <div className="mt-4">
          <p className="text-lg">Signed in as: <strong>{session.user?.email}</strong></p>
          <button
            onClick={() => signOut()}
            className="mt-2 bg-red-500 text-white px-4 py-2 rounded"
          >
            Sign Out
          </button>
        </div>
      ) : (
        // If user is not signed in, show Sign In and Sign Up options
        <div className="mt-4 flex gap-4">
          <button
            onClick={() => signIn()}
            className="bg-blue-500 text-white px-4 py-2 rounded"
          >
            Sign In
          </button>
          <a href="/signup" className="bg-green-500 text-white px-4 py-2 rounded">
            Sign Up
          </a>
        </div>
      )}
    </div>
  );
}
